import React from 'react'

require('../../../src/scss/components/_button-cyber.scss');

export default function ButtonCyber({text, href, bonusClass}) {
    let element = ''
    if (href) {
        element = `
            <a href="${href}" class="btn-cyber ${bonusClass}">
                <span class="btn-cyber__text" data-text="${text}">${text}</span>
                <span class="btn-cyber--glitch"></span>
            </a>
            `
    } else {
        element = `
            <button class="btn-cyber ${bonusClass}">
                <span class="btn-cyber__text" data-text="${text}">${text}</span>
                <span class="btn-cyber--glitch"></span>
            </button>
            `
    }
    return (
        <div dangerouslySetInnerHTML={{__html: element}}/>
    )
}
