import React from "react";
import {graphql, Link, useStaticQuery} from "gatsby"
import {Slide} from "react-slideshow-image";

const ClientCarousel = () => {
    const responsiveSettings = [
        {
            breakpoint: 800,
            settings: {
                slidesToShow: 4,
                slidesToScroll: 1
            }
        },
    ];
    const data = useStaticQuery(graphql`
    query {
      allWpPost(
        sort: {fields: [date], order: DESC}
        filter: {categories: {nodes: {elemMatch: {name: {eq: "clients"}}}}}

      ) {
          nodes {
            id
            title
            excerpt
            language {
              slug
            }
            featuredImage {
              node {
                sourceUrl
                 localFile {
                   publicURL
                   childImageSharp {
                     gatsbyImageData(
                        placeholder: BLURRED
                     )
                     fixed(width: 100, quality: 100) {
                         ...GatsbyImageSharpFixed
                     }
                   }
                 }
              }
            }
          }
      }
    }
  `)
    return (
        <div className="pt-8 overflow-hidden">
            <Slide slidesToShow={1} duration={0} transitionDuration={2000} arrows={false} autoplay={true} infinite={true} indicators={false} responsive={responsiveSettings}>
                {data.allWpPost.nodes.map((node) => (
                    <div className="mx-8 css--partners-logo">
                        <img src={node.featuredImage.node.localFile.publicURL} alt="Client image"/>
                    </div>
                ))}
            </Slide>
        </div>
    )
}
export default ClientCarousel;


