import React from "react";
import {useTranslation} from "react-i18next";
export default function Bonuses() {
    const {t} = useTranslation();
    return (
        <div className="md:container mx-auto md:min-h-screen flex justify-center items-center align-middle relative">
            <div className="grid grid-cols-1 lg:grid-cols-12 lg:gap-28 flex justify-between  pt-32 md:pt-0">
                <div className="lg:col-span-6 lg:pl-10 flex flex-col justify-between">
                    <h1 className="text-3xl md:text-5xl glitch text-center pb-16 mb-0" data-glitch={t('outsourcing_bonuses_title_one')}>
                        <span className="title-underline">
                            {t('outsourcing_bonuses_title_one')}
                        </span>
                    </h1>
                    <div className="css--outsourcing-bonuses-square">
                        <ul>
                            <li>Option to have 1:1 meeting with resource.</li>
                            <li>Transparency in all our processes to ensure quality of our services.</li>
                            <li>Weekly calls about the progress.</li>
                            <li>Portal to register your issues or new features.</li>
                        </ul>
                    </div>
                </div>
                <div className="lg:col-span-6 lg:pr-10 pt-20 lg:pt-0 flex flex-col justify-between">
                    <h1 className="text-3xl md:text-5xl glitch text-center pb-16 mb-0" data-glitch={t('outsourcing_bonuses_title_two')}>
                        <span className="title-underline">
                             {t('outsourcing_bonuses_title_two')}
                        </span>
                    </h1>
                    <div className="css--outsourcing-bonuses-square">
                        <ul>
                            <li>No-prepayment required, we have flexible payment options.</li>
                            <li>We dont charge for bugs which you catch.</li>
                            <li>15% Discount for long term partnership.</li>
                            <li>Reduce risk- First week we give -50% discount and if you are not satisfied with our services - we are not going to charge you.</li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    )
}
