import React from "react";
import 'react-slideshow-image/dist/styles.css';
import Calendly from "../../elements/Calendly";

export default function Meeting() {
    return (
        <div className="md:container mx-auto md:min-h-screen flex flex-col justify-center align-middle relative pt-24 lg:pt-0">
            <h1 className="text-3xl md:text-5xl glitch text-center mb-0" data-glitch="Book a free meeting to achieve new results">
                <span className="title-underline">
                        Book a free meeting to achieve new results
                </span>
            </h1>
            <div className="flex justify-center">
                <div>
                    <div className="css--calendly-box rounded-lg">
                        <Calendly/>
                    </div>
                    <div className="css--outsourcing-meeting-info">
                        <div className="title">
                            Cant find your time?
                        </div>
                        <div className="contact flex justify-center">
                            <div className="flex mr-2">
                                <img src={"/images/icons/email.svg"} alt="Email Logo"/>
                                <span>it@kalmars.lv</span>
                            </div>
                            <div className="flex ml-2">
                                <img src={"/images/icons/phone.svg"} alt="Email Logo"/>
                                <span>+371 27797931</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
