import React from "react";
import {Slide} from 'react-slideshow-image';
import 'react-slideshow-image/dist/styles.css';

export default function Offers() {
    const responsiveSettings = [
        {
            breakpoint: 800,
            settings: {
                slidesToShow: 3,
                slidesToScroll: 1
            }
        },
    ];
    return (
        <div className="md:container mx-auto md:min-h-screen flex flex-col justify-center align-middle relative lg:mt-0 mt-24 overflow-hidden">
            <h1 className="text-3xl md:text-5xl glitch text-center mb-0 " data-glitch="What we can offer">
                <span className="title-underline">
                      What we can offer
                </span>
            </h1>
            <Slide slidesToShow={1} arrows={false} infinite={true} indicators={false} responsive={responsiveSettings}>
                <div>
                    <div className="css--outsourcing-offers-box mx-1">
                        <div className="title">Web development
                        </div>
                        <div className="desc">Experienced frontend and backend developers create future-proof web-apps. User experience is at the core, driving all the decisions taken throughout the development lifecycle. Use of modern frameworks and cloud-first philosophy bring scalability and simplified iterations. The dedicated manager delivers personalisation and efficient communications.</div>
                    </div>
                </div>
                <div>
                    <div className="css--outsourcing-offers-box mx-1">
                        <div className="title">Mobile development
                        </div>
                        <div className="desc">Hybrid apps for Android and iOS offer flawless performance and immaculate user experience. Underlying databases and back-end solutions are fast and resilient. DevOps integrates orchestration, micro-services and containerisation to offer scalability. The full-cycle team extends branding and marketing insights across website and mobile assets</div>
                    </div>
                </div>
                <div>
                    <div className="css--outsourcing-offers-box mx-1">
                        <div className="title">E-commerce development</div>
                        <div className="desc">The dedicated team helps in the development of online store, CRM, payment systems and any software that will increase cash flow in eCommerce. In addition, KALMARS can increase your capacity, improve the quality of planning and facilitate the coordination of work for different executive groups in eCommerce.</div>
                    </div>
                </div>
            </Slide>
        </div>
    )
}
