import React from "react";
import {Slide} from "react-slideshow-image";
import 'react-slideshow-image/dist/styles.css';
import {graphql, useStaticQuery} from "gatsby";
import {useTranslation} from "react-i18next";
import ClientCarousel from "../../elements/ClientCarousel";


export default function Testemonials() {
    const {i18n} = useTranslation();
    const data = useStaticQuery(graphql`
    query {
      allWpPost(
        sort: {fields: [date], order: DESC}
        filter: {categories: {nodes: {elemMatch: {name: {eq: "testimonials"}}}}}
      ) {
          nodes {
            id
            title
            content
            date(formatString: "YYYY/MM/DD")
            excerpt
            tags {
              nodes {
                name
              }
            }
            language {
              slug
            }
            featuredImage {
              node {
                sourceUrl
                 localFile {
                   publicURL
                   childImageSharp {
                     gatsbyImageData(
                        placeholder: BLURRED
                     )
                     fixed(width: 100, quality: 100) {
                         ...GatsbyImageSharpFixed
                     }
                   }
                 }
              }
            }
          }
      }
    }
  `)
    const responsiveSettings = [
        {
            breakpoint: 800,
            settings: {
                slidesToShow: 2,
                slidesToScroll: 1
            }
        },
    ];
    return (
        <div className="md:container mx-auto md:min-h-screen flex flex-col justify-center align-middle relative pt-24 lg:pt-0">
            <h1 className="text-3xl md:text-5xl glitch text-center mb-0" data-glitch="Testemonials">
                <span className="title-underline">
                     Testemonials
                </span>
            </h1>
            <div className="pt-8 overflow-hidden">
                <Slide slidesToShow={1} arrows={false} autoplay={true} infinite={true} indicators={false} responsive={responsiveSettings}>
                    {data.allWpPost.nodes.map((node, index) => (
                        node.language.slug === i18n.language &&
                        <div className="css--outsourcing-testemonial-box relative mx-8">
                            <p className="text-white text-justify" dangerouslySetInnerHTML={{__html: node.content}}/>
                        </div>
                    ))}
                </Slide>
            </div>
            <div className="mt-24">
                <ClientCarousel/>
            </div>
        </div>
    )
}
